<template>
  <view class="container">
    <map 
      id="myMap" 
      :latitude="latitude" 
      :longitude="longitude" 
      :polyline="polyline" 
      show-location
      style="width: 100%; height: 70vh;">
    </map>
    
    <view class="control-panel">
      <text>{{ formattedTime }}</text>
      <text>距离：{{ distance }}公里</text>
      <text>热量：{{ calories }}千卡</text>
      <button @click="toggleRun">{{ isRunning ? '停止' : '开始跑步' }}</button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      isRunning: false,
      startTime: null,
      timer: null,
      seconds: 0,
      
      // 定位数据
      latitude: 39.90923,
      longitude: 116.397428,
      locations: [],
      
      // 运动数据
      distance: 0,
      calories: 0,
      
      // 地图轨迹
      polyline: [{
        points: [],
        color: "#FF0000DD",
        width: 6
      }]
    }
  },
  computed: {
    formattedTime() {
      const pad = num => num.toString().padStart(2, '0')
      return `${pad(Math.floor(this.seconds/3600))}:${pad(Math.floor(this.seconds%3600/60))}:${pad(this.seconds%60)}`
    }
  },
  mounted() {
    // 检查地图是否初始化成功
    uni.getMapManager('AMap', (manager) => {
      console.log('地图管理器:', manager) // 应该输出SDK实例
    });
  },
  methods: {
    toggleRun() {
      if(this.isRunning) {
        this.stopRun()
      } else {
        this.startRun()
      }
    },
    
    async startRun() {
      this.isRunning = true
      this.startTime = Date.now()
      this.timer = setInterval(() => {
        this.seconds = Math.floor((Date.now() - this.startTime) / 1000)
      }, 1000)
      
      // 开始监听位置
      this.watchId = uni.startLocationUpdate({
        success: () => console.log('监听位置成功'),
        fail: err => console.error(err)
      })
      
      uni.onLocationChange(res => {
        this.updatePosition(res)
      })
    },
    
    stopRun() {
      this.isRunning = false
      clearInterval(this.timer)
      uni.stopLocationUpdate()
      
      // 保存运动记录
      this.saveRecord()
    },
    
    updatePosition(position) {
      const newPoint = {
        latitude: position.latitude,
        longitude: position.longitude
      }
      
      // 计算距离
      if(this.locations.length > 0) {
        const last = this.locations[this.locations.length - 1]
        this.distance += this.calcDistance(last, newPoint)
      }
      
      // 更新位置数据
      this.locations.push(newPoint)
      this.latitude = position.latitude
      this.longitude = position.longitude
      
      // 更新轨迹
      this.polyline[0].points = [...this.polyline[0].points, newPoint]
      
      // 计算卡路里（示例算法）
      this.calories = (this.distance * 65).toFixed(2) // 假设体重65kg
    },
    
    // 计算两点间距离（Haversine公式）
    calcDistance(p1, p2) {
      const R = 6371; // 地球半径（公里）
      const dLat = (p2.latitude - p1.latitude) * Math.PI / 180
      const dLon = (p2.longitude - p1.longitude) * Math.PI / 180
      const a = 
        Math.sin(dLat/2) * Math.sin(dLat/2) +
        Math.cos(p1.latitude * Math.PI / 180) * 
        Math.cos(p2.latitude * Math.PI / 180) *
        Math.sin(dLon/2) * Math.sin(dLon/2)
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a))
      return R * c
    },
    
    saveRecord() {
      // 保存到本地或发送到服务器
      const record = {
        date: Date.now(),
        duration: this.seconds,
        distance: this.distance,
        calories: this.calories,
        path: this.locations
      }
      console.log('保存记录：', record)
    }
  }
}
</script>

<style>
.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.control-panel {
  padding: 20rpx;
  background: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
</style>